import { Component, OnInit, OnDestroy } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';

import { AuthService } from '../auth.service';

import { ToastrService } from 'ngx-toastr';
import { NgxSpinnerService } from 'ngx-spinner';

import { AuthModel, Authresonse } from '../auth.model';
import { Router } from '@angular/router';
import { Subscription, Observable } from 'rxjs';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss'],
})
export class LoginComponent implements OnInit, OnDestroy {
  errorSub: Subscription;
  constructor(
    private fb: FormBuilder,
    private toastr: ToastrService,
    private spinner: NgxSpinnerService,
    private authService: AuthService,
    private router: Router
  ) {}

  form = this.fb.group({
    phone: ['1', [Validators.required]],
    // email: ['test@test.com', [Validators.required, Validators.email]],
    password: ['test', Validators.required],
  });

  ngOnInit() {
    this.errorSub = this.authService.error.subscribe((err: string) => {
      console.log('errorsub in ngoninit');
      console.log(err);
      this.toastr.error(err, 'Error');
    });
  }
  ngOnDestroy(): void {
    this.errorSub.unsubscribe();
  }
  onSubmit(form: AuthModel) {
    this.spinner.show();
    let authObs: Observable<Authresonse>;
    // this.isLoading = true;
    this.authService.postForm('auth/login', form).subscribe(
      (res) => {
        if (res) {
          if (!res['admin'] && !res['owner']) {
            this.toastr.error('Not authorized', 'Error');
          }
          console.log(res['message']);
          this.router.navigate(['/']);
        }
      },
      (errorRes) => {
        console.log('error');
        // this.toastr.error(errorRes.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );
    this.spinner.hide();
  }
}
